<template>
	<div class="headers">

		<cheader></cheader>

		<div class="main help">
			<div class="sub_nav">
				<a href="javascript:;">首页</a>
				<em>&gt;</em>
				<a href="/index.php/index/help_center" style="color:#0f95f6">帮助中心</a>
			</div>

			<div class="help_main">
				<div class="fl help_sub_left" id="help_fenlei">

					<div class="sub_item" v-for="(item,index) in wentype" @click="isshwt(index)">
						<div class="nav_title">
							<a href="javascript:;"><i class="iconfont icon-sub"></i>
								<span></span>{{item}}<em class="sj hover"></em></a>
						</div>
					</div>
					
				</div>

				<div class="fr help_right ">

					<div class="zh" v-show="isshow == 0">

						<div class="item" id="Number1" v-for="item in wentis">
							<a name="a2"></a>
							<div class="accordion-header">
								<div class="accordion-header">
									<div class="item_title">
										{{item.wen}}
									</div>
								</div>
							</div>
							<div class="item_content  accordion-content">
								<p  v-for="items in item.wenl" ><span style="font-family: 微软雅黑, 宋体, Verdana; font-size: 12px; background-color: rgb(255, 255, 255);">· {{items}}</span></p>
							</div>
						</div>

						

					</div>
					
					
					
					<div class="zh" v-show="isshow == 1">

						<div class="item" id="Number1" v-for="item in wentist">
							<a name="a2"></a>
							<div class="accordion-header">
								<div class="accordion-header">
									<div class="item_title">
										{{item.wen}}
									</div>
								</div>
							</div>
							<div class="item_content  accordion-content">
								<p  v-for="items in item.wenl" ><span style="font-family: 微软雅黑, 宋体, Verdana; font-size: 12px; background-color: rgb(255, 255, 255);">· {{items}}</span></p>
							</div>
						</div>

						

					</div>
					
					
					
					
					
				</div>
			</div>
		</div>

		<footers></footers>

	</div>
</template>

<script>
	import cheader from '../../components/common/header';
	import footers from '../../components/common/footer';
	export default {
		data() {
			return {
				isshow:0,
				wentis:[
					{
						wen:'1',
						wenl:['你是？','你是？']
					},
					{
						wen:'2',
						wenl:['我是？','你是？']
					}
				],
				wentist:[
					{
						wen:'1',
						wenl:['哈喽？','哈喽？']
					},
					{
						wen:'2',
						wenl:['我是？','你是？']
					}
				],
				wentype:['常见问题','个人账号','充值提款','规则说明','新手上路']
			}
		},
		methods: {
			isshwt(star){
				let that = this;
				that.isshow = star
			}
		},
		mounted: function() {
			$('.zh .item').click(function(){
				$(this).find('.item_content').slideToggle();
			})
		},
		components: {
			cheader,
			footers
		}
	}
</script>
<style rel="stylesheet" type="text/css" src="../../style/bzzx.css" scoped="scoped" /></style>


